@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import "components/_inputs.scss";
@import "components/_pagination.scss";
@import "components/_tabs.scss";
@import "components/_grid.scss";
._scale-img{
  overflow: hidden;
  &:hover{
    img{
      transform: scale(1.05);
    }
  }
  img{
    transition: all .6s;
  }
}

.m-backdrop{
  position: relative;
  &:after{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
    pointer-events: none;
    border-radius: inherit;
  }
}

.socials{
  display: flex;
  align-items: center;
  &__item{
    width: 2em;
    @include lastM(0 .75em 0 0);
    @include center;
    img{
      width: 100%;
      display: block;
      transition: all .4s;
    }
    &:hover{
      img{
        transform: translateY(-.5em);
      }
    }
  }
}

.compare-btn,
.favourite-btn{
  @include center;
  svg{
    width: 100%;
    height: auto;
  }
}

.favourite-btn{
  width: 1.4375em;
  &:hover{
    path{
      opacity: 1;
    }
  }
  &._added{
    path{
      opacity: 1;
      stroke: $primary;
      fill: $primary;
    }
  }
}

.compare-btn{
  width: 1.5em;
  &:hover{
    g{
      opacity: 1;
    }
  }
  &._added{
    g{
      opacity: 1;
    }
    line{
      stroke: $primary;
    }
  }
}
