.process{
  .m-section__top{
    max-width: 68.25em;
  }
}

.process-card{
  &__img{
    @include imgCover(62.3%);
    border-radius: 1em;
  }
  &__content{
    margin-top: 1.5em;
    @include grid100gap(.75em);
  }
}

.tree-card{
  text-align: center;
  &__icon{
    display: flex;
    justify-content: center;
    height: 5em;
    img{
      height: 100%;
      display: block;
      width: auto;
    }
  }
  &__content{
    margin-top: 1.5em;
    @include grid100gap(1em);
  }
  &__desc{
    color: $greyDark;
  }
}

.service-info{
  .text{
    margin-top: 1.5em;
  }
  &__img,
  &__video{
    height: 100%;
    img,
    iframe{
      object-fit: cover;
      display: block;
      width: 100%;
      height: 100%;
      border-radius: 1em;
    }
  }
}

.roof{
  .h2{
    @media screen and (min-width: 993px){
      font-size: 3em;
    }
  }
}

.roof-card{
  color: $white;
  position: relative;
  &__title{
    @include centerAbsolute;
    padding: 1em;
    z-index: 3;
    width: 100%;
    text-align: center;
  }
  &__img{
    @include imgCover(51%);
    border-radius: .75em;
    &:after{
      background: rgba(0,0,0,.4);
    }
  }
}


@media screen and (max-width: 1400px){
  .process-card{
    &__content{
      margin-top: 1em;
      gap: .5em;
    }
  }

  .tree-card{
    &__icon{
      height: 4em;
    }
    &__content{
      margin-top: 1.25em;
      gap: .5em;
    }
  }
}


@media screen and (max-width: 992px){
  .roof{
    &__grid{
      gap: 2.5em;
    }
  }
}


@media screen and (max-width: 576px){
  .tree-card {
    &__icon {
      height: 3.5em;
    }
  }

  .service-info{
    &__video{
      iframe{
        min-height: 11.25em;
      }
    }
    .text{
      margin-top: 1em;
    }
  }

  .roof{
    &__items{
      gap: .75em;
    }
  }
}
