.footer{
  padding-top: 5em;
  &__logo{
    display: block;
    width: 8.4375em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__top{
    padding: .75em 0 1.5em;
    display: flex;
    justify-content: space-between;
  }
  &__text{
    font-size: 1.125em;
    line-height: 120%;
    letter-spacing: 0.01em;
    color: #464646;
  }
  &__bottom{
    border-top: $border;
    padding: 1.5em 0;
    display: flex;
    justify-content: space-between;
  }
  &__c,
  &__conf,
  &__by{
    font-size: 1em;
    line-height: 120%;
    letter-spacing: 0.02em;
    color: #716969;
  }
}

.footer-info{
  max-width: 22em;
}

.footer-contacts{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-top: 2em;
  &__link{
    @include lastM(0 0 1em);
    font-size: 1em;
    line-height: 120%;
    letter-spacing: 0.02em;
    font-weight: 500;
  }
}

.footer-nav{
  flex-grow: 1;
  max-width: 66.25em;
  &__col{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__link{
    font-size: 1.125em;
    letter-spacing: 0.02em;
    color: $dark;
    @include lastM(0 0 1.33em);
  }
}


@media screen and (max-width: 1400px){
  .footer{
    &__logo{
      width: 7.5em;
    }
    &__bottom{
      padding-bottom: 1em;
    }
  }

  .footer-info{
    max-width: 22em;
  }

  .footer-nav{
    max-width: 50em;
    display: flex;
    justify-content: space-between;
    gap: unset;
    &__col{
      max-width: 12em;
    }
    &__link{
      font-size: 1em;
      @include lastM(0 0 1em);
    }
  }

  .footer-contacts{
    margin-top: 2em;
  }
}


@media screen and (max-width: 992px){
  .footer{
    padding-top: 2.5em;
    &__top{
      display: block;
    }
    &__text{
      font-size: 1em;
    }
    &__c,
    &__conf,
    &__by{
      font-size: .875em;
      letter-spacing: 0.01em;
    }
  }

  .footer-contacts{
    margin-top: 1.5em;
  }

  .footer-nav{
    margin-top: 1.5em;
    padding-top: 1.5em;
    border-top: $border;
  }
}


@media screen and (max-width: 576px){
  .footer{
    &__bottom{
      flex-direction: column;
      align-items: flex-start;
    }
    &__c,
    &__conf{
      margin-bottom: 1.14em;
    }
  }

  .footer-nav{
    display: block;
    &__col{
      @include lastM(0 0 1.25em);
      max-width: 100%;
      br{
        display: none;
      }
    }
    &__link{
      @include lastM(0 0 1.25em);
    }
  }
}
