.guarantee{
  &__inner{
    padding: 7.5em;
    border-radius: 1em;
    background-image: url(../img/guarantee.webp);
    background-size: cover;
    background-position: center;
    color: $white;
    text-align: center;
  }
  &__content{
    position: relative;
    z-index: 2;
    width: 100%;
    max-width: 60em;
    margin: 0 auto;
  }
  &__info{
    @include grid100gap(.75em);
  }
  &__desc{
    max-width: 41em;
    margin: 0 auto;
  }
  &__nav{
    margin-top: 2em;
    display: flex;
    justify-content: center;
  }
  &__btn{
    width: 32.5em;
    max-width: 100%;
  }
}


@media screen and (max-width: 1400px){
  .guarantee{
    &__inner{
      padding: 4em;
      background-image: url(../img/guarantee-laptop.webp);
    }
  }
}


@media screen and (max-width: 992px){
  .guarantee{
    &__inner{
      padding: 2.5em 1.5em;
      border-radius: .75em;
    }
  }
}


@media screen and (max-width: 576px){
  .guarantee{
    &__inner{
      background-image: url(../img/guarantee-mob.webp);
    }
    &__nav{
      margin-top: 1.5em;
    }
    &__btn{
      height: 2.75em;
    }
  }
}
