.main{
  padding: 3em 0 5em;
}

.main-slider{
  overflow: hidden;
  border-radius: 1em;
  position: relative;
  .swiper{
    &-pagination{
      bottom: 1.5em;
      &-bullet{
        background-color: transparent;
        border: 2px solid $black;
        width: .75em;
        height: .75em;
        margin: 0 .25em;
        opacity: 1;
        &-active{
          background-color: $black;
        }
      }
    }
    &-button{
      width: 2em;
      height: 2em;
      position: static;
    }
  }
}

.main-banner{
  border-radius: 1em;
  padding: 11.375em 8.5em;
  &__content{
    max-width: 40em;
    position: relative;
    z-index: 2;
  }
  &__img{
    width: 100%;
    height: 100%;
    border-radius: inherit;
    position: absolute;
    top: 0;
    right: 0;
    img{
      width: 100%;
      height: 100%;
      display: block;
      object-fit: cover;
      border-radius: inherit;
    }
  }
  &__desc{
    margin-top: 1.5em;
  }
  &__nav{
    display: flex;
    margin-top: 2.5em;
  }
  &__btn{
    height: 3.125em;
    border-radius: .5em;
    font-weight: 400;
  }
}

.m-section{
  padding: 5em 0;
  &.m-section--first{
    padding-top: 0;
  }
  &__top{
    margin-bottom: 3em;
    @include grid100gap(1em);
  }
}

.service-card{
  border-radius: .75em;
  color: $smeshariki;
  font-weight: 500;
  display: flex;
  flex-direction: column;
  background: #f4f4f4;
  &__img{
    border-radius: .75em .75em 0 0;
    @include imgCover(88.3%);
  }
  &__content{
    text-align: center;
    padding: 1.5em;
  }
  &__title{
    line-height: 110%;
    letter-spacing: 0.02em;
  }
}


@media screen and (max-width: 1400px){
  .main{
    padding-top: 2em;
  }

  .main-slider{
    .swiper {
      &-pagination {
        bottom: 1.25em;
      }
    }
  }

  .main-banner{
    padding: 4.75em 6.375em;
    &__content{
      max-width: 33.75em;
    }
    &__desc{
      margin-top: 1.25em;
    }
    &__nav{
      margin-top: 2em;
    }
    &__btn{
      height: 3em;
    }
  }

  .m-section{
    &__top{
      margin-bottom: 2em;
    }
  }

  .service-card{
    &__content{
      padding: 1em;
    }
  }
}


@media screen and (max-width: 992px){
  .main{
    padding: .5em 0 2.5em;
  }

  .main-slider{
    .swiper {
      &-pagination {
        bottom: 2.5em;
      }
    }
  }

  .main-banner{
    padding: 2.5em 1.5em 4.375em;
    &__desc{
      margin-top: 1em;
    }
    &__img{
      &:before{
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        border-radius: inherit;
        background-color: rgba($white,.4);
      }
    }
  }

  .m-section{
    padding: 2.5em 0;
    &__top{
      margin-bottom: 1em;
      gap: .75em;
    }
  }

  .service-card{
    &__img{
      padding-bottom: 48.7%;
    }
    &__content{
      padding: 1em 1.5em;
      min-height: 4.25em;
    }
  }
}


@media screen and (max-width: 576px){
  .main-slider{
    .swiper {
      &-pagination {
        &-bullet{
          width: .625em;
          height: .625em;
          border-width: 1px;
        }
      }
    }
  }

  .main-banner{
    &__desc{
      max-width: 16.875em;
    }
    &__btn{
      width: 100%;
    }
  }

  .services{
    &__grid{
      gap: 1em;
    }
  }
}
