.service{
  &__inner{
    padding: 5.625em 8.5em;
    background-color: $gray;
    border-radius: 1em;
    position: relative;
    overflow: hidden;
    &:before{
      content: '';
      position: absolute;
      width: 65.125em;
      height: 65.125em;
      border-radius: 50%;
      background-color: rgba(255, 255, 255, 0.8);
      filter: blur(160px);
      left: -7.25em;
      top: 50%;
      transform: translateY(-50%);
      z-index: 2;
    }
  }
  &__content{
    max-width: 49.375em;
    position: relative;
    z-index: 3;
  }
  &__bg{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: inherit;
    &._view2{
      display: none;
    }
  }
  &__items{
    margin-top: 1.5em;
  }
  &__nav{
    display: flex;
    margin-top: 3em;
  }
  &__btn{
    width: 24.0625em;
    max-width: 100%;
  }
}

.service-item{
  padding-left: 3.5em;
  position: relative;
  min-height: 2.5em;
  &:before{
    content: '';
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    width: 2.5em;
    height: 2.5em;
    background-image: url(../img/check2.svg);
    @include bgImgSize(100%);
  }
  span{
    font-size: 1.25em;
    line-height: 120%;
    letter-spacing: 0.01em;
  }
}

.variant-card{
  border-radius: 1em;
  padding: 2em;
  //background-size: cover;
  //background-position: center;
  text-align: center;
  color: $white;
  &:after{
    background: rgba(0,0,0,.6);
  }
  &__bg{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    display: block;
    border-radius: inherit;
    &._view2,
    &._view3{
      display: none;
    }
  }
  &__inner{
    position: relative;
    z-index: 2;
    @include grid100gap(.75em);
  }
  &__num{
    font-size: 3em;
    letter-spacing: 0.01em;
    font-weight: 600;
    color: $primary;
  }
  &__title{
    font-size: 1.75em;
    font-weight: 600;
    letter-spacing: 0.01em;
    margin-top: .14em;
  }
}

.service-detail{
  .m-section__top{
    max-width: 77.25em;
  }
  &__grid{
    display: grid;
    grid-template-columns: 32.5em calc(100% - 32.5em - 1.25em);
    gap: 1.5em 1.25em;
    @include lastM(0 0 1.25em);
  }
  &__img{
    border-radius: .75em;
    width: 100%;
    display: block;
    min-height: 100%;
    object-fit: cover;
  }
  &__text{
    p,li{
      font-size: 1.25em;
      line-height: 140%;
      letter-spacing: 0.01em;
    }
    p{
      @include lastM(0 0 .75em);
    }
    ul{
      @include lastM(0 0 1em);
    }
    ul{
      li{
        &:before{
          content: '- ';
        }
      }
    }
    ._mark{
      padding: .2em .4em;
      background-color: $rickAndMorty;
      border-radius: .4em;
    }
  }
}


@media screen and (max-width: 1400px){
  .service{
    &__inner{
      padding: 5em 6.375em;
    }
    &__btn{
      width: 17.8125em;
    }
  }

  .variant-card{
    padding: 2em 1.5em;
    &__bg{
      &._view1{
        display: none;
      }
      &._view2{
        display: block;
      }
    }
    &__inner{
      gap: 1em;
    }
    &__num{
      font-size: 2.5em;
    }
    &__title{
      margin: 0;
      font-size: 1.5em;
    }
  }

  .service-detail{
    &__grid{
      grid-template-columns: 24.1875em calc(100% - 24.1875em - 1.25em);
    }
    &__text{
      p,li{
        font-size: 1.125em;
      }
      p{
        @include lastM(0 0 .89em);
      }
      ._mark{
        padding: .22em .44em;
        border-radius: .44em;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .service{
    &__inner{
      padding: 2.5em 1.5em;
      &:before{
        width: 100%;
        height: 100%;
        border-radius: inherit;
        top: 0;
        left: 0;
        transform: unset;
        background-color: rgba(255,255,255,.7);
        filter: unset;
      }
    }
    &__nav{
      margin-top: 1.5em;
    }
    &__items{
      grid-template-columns: 100%;
      gap: .75em;
    }
  }

  .service-item{
    padding-left: 2.25em;
    padding-top: .03125em;
    min-height: 1.25em;
    &:before{
      top: 0;
      transform: unset;
      width: 1.25em;
      height: 1.25em;
    }
    br{
      display: none;
    }
    span{
      font-size: 1em;
      line-height: 120%;
    }
  }

  .variant-card{
    padding: 2.5em 1.5em;
    &__bg{
      &._view2{
        display: none;
      }
      &._view3{
        display: block;
      }
    }
    &__num{
      font-size: 2em;
    }
    &__title{
      font-size: 1.25em;
    }
  }

  .service-detail{
    &__grid{
      grid-template-columns: 100%;
      @include lastM(0 0 1  em);
    }
    &__text{
      p,li{
        font-size: 1em;
      }
      p{
        @include lastM(0 0 1em);
      }
      ._mark{
        padding: .25em .5em;
        border-radius: .5em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .service{
    &__btn{
      height: 3em;
      width: 100%;
    }
    &__bg{
      &._view1{
        display: none;
      }
      &._view2{
        display: block;
      }
    }
  }
  .variants{
    &__grid{
      grid-template-columns: 100%;
      gap: .75em;
    }
  }
}
