.error404{
  padding-top: 10em;
  &__inner{
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__img{
    width: 49.375em;
    display: block;
    max-width: 100%;
    margin-bottom: 5em;
  }
  &__desc{
    color: $greyDark;
  }
  &__link{
    margin-top: 1.5em;
    width: 15.625em;
    span{
      font-size: 1.125em;
    }
  }
}


@media screen and (max-width: 1400px){
  .error404{
    &__img{
      margin-bottom: 3.5em;
    }
    &__link{
      margin-top: 1.25em;
    }
  }
}


@media screen and (max-width: 992px){
  .error404{
    padding-top: 5em;
    &__img{
      margin-bottom: 2em;
    }
  }
}


@media screen and (max-width: 576px){
  .error404{
    &__link{
      width: 100%;
      height: 3em;
      span{
        font-size: 1em;
      }
    }
  }
}
