.text{
  p{
    font-size: 1.5em;
    line-height: 130%;
    letter-spacing: .01em;
    @include lastM(0 0 1em);
  }
  ul{
    @include lastM(0 0 1.5em);
    @include grid100gap(1em);
    li{
      font-size: 1.25em;
      line-height: 130%;
      letter-spacing: .01em;
      padding-left: 1.8em;
      position: relative;
      &:before{
        content: '';
        position: absolute;
        width: .75em;
        height: .75em;
        border-radius: 50%;
        background-color: $primary;
        top: .3em;
        left: .2em;
      }
    }
  }
}


@media screen and (max-width: 1400px){
  .text{
    p{
      font-size: 1.25em;
      @include lastM(0 0 1.25em);
    }
    ul{
      gap: 1.25em;
    }
  }
}


@media screen and (max-width: 992px){

}


@media screen and (max-width: 576px){
  .text{
    p{
      font-size: 1em;
      @include lastM(0 0 1em);
    }
    ul{
      @include lastM(0 0 1em);
      gap: .75em;
      li{
        font-size: 1em;
      }
    }
  }
}
