.contacts2{
  padding-top: 0;
  @media screen and (min-width: 993px){
    .contacts__grid{
      grid-template-columns: 41em 49.375em;
    }
    .contacts__img{
      padding-bottom: 47.85%;
    }
    .contacts__items{
      padding-top: 1.5em;
      margin-top: 1.5em;
      border-top: $border;
      gap: 1em;
    }
    .contact-item{
      display: flex;
      align-items: center;
      justify-content: space-between;
      &__title,
      &__desc{
        font-size: 1.125em;
        font-weight: 400;
        letter-spacing: 0.03em;
      }
    }
  }
  &__bottom{
    margin-top: auto;
    padding-top: 1.5em;
  }
  &__city{
    font-size: 1.75em;
    line-height: 140%;
    letter-spacing: 0.03em;
    text-transform: uppercase;
  }
}


@media screen and (max-width: 1400px){
  .contacts2{
    @media screen and (min-width: 993px) {
      .contacts__grid {
        grid-template-columns: 36.875em 30.5em;
      }
      .contacts__img{
        padding-bottom: 67.6%;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .contacts2{
    &__city{
      font-size: 1.125em;
    }
    .contacts__items{
      margin-top: 1em;
      padding-top: 1em;
      border-top: $border;
    }
  }
}
