.page-top{
    padding: 1.5em 0 3em;
}

.breadcrumbs {
    &__item {
        position: relative;
        display: inline;
        font-size: .875em;
        line-height: 130%;
        letter-spacing: .01em;
        color: #716969;
        &:not(:first-child) {
            margin-left: .75em;
            &:before {
                content: '/';
                position: absolute;
                z-index: 1;
                top: .125em;
                right: 100%;
                margin-right: .25em;
                color: #716969;
            }
        }
        &:last-child{
            pointer-events: none;
            color: #2b2929;
        }
    }
}


@media screen and (max-width: 992px){
    .page-top{
        opacity: 0;
        height: .5em;
        padding: 0;
        overflow: hidden;
        pointer-events: none;
    }
}
