@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 2.5em;
    height: 3.4375em;
    border-radius: .75em;
    font-weight: 500;
    &:focus {
        box-shadow: none;
    }
    span{
        letter-spacing: 0.02em;
        font-size: 1em;
        line-height: 150%;
    }
}

.m-btn-primary{
    background-color: $primary;
    color: $white;
    &:hover{
        background-color: darken($primary,20%);
        color: $white;
    }
}
