.about{
  &__grid{
    display: grid;
    grid-template-columns: calc(100% - 49.375em - 9.6875em) 49.375em;
    justify-content: space-between;
  }
  &__img{
    @include imgCover(49.6%);
    border-radius: 1em;
  }
  &__desc{
    margin-top: 1.5em;
    max-width: 37.5em;
  }
  &__items{
    display: flex;
    margin-top: 2.5em;
  }
}

.about-item{
  flex-grow: 1;
  padding: 1em 2em;
  margin-right: 1.125em;
  border-radius: .75em;
  background-color: $primary;
  color: $white;
  text-align: center;
  &:last-child{
    margin: 0;
  }
  &__num{
    font-size: 2.5em;
    line-height: 110%;
    letter-spacing: 0.01em;
    font-weight: 500;
  }
  &__title{
    font-size: 1em;
    line-height: 140%;
    letter-spacing: 0.02em;
    text-transform: uppercase;
    margin-top: .25em;
  }
}

.subscribe{
  &__inner{
    padding: 6.4375em;
    border-radius: 1em;
    background-color: $rickAndMorty;
    background-image: url(../img/subscribe-bg.svg);
    background-size: cover;
    background-position: center;
  }
  &__text{
    text-align: center;
    @include grid100gap(.5em);
  }
  &__desc{
    font-size: 1.125em;
    line-height: 110%;
    letter-spacing: .01em;
    color: $greyDark;
  }
}

.subscribe-form{
  margin: 1.5em auto 0;
  width: 32.5em;
  max-width: 100%;
  .fg{

  }
  &__btn{
    margin-top: 1.5em;
    width: 100%;
  }
  .m-check{
    margin-top: .5em;
  }
}

.contacts {
  &__grid {
    display: grid;
    grid-template-columns: calc(100% - 57.8125em - 1.25em) 57.8125em;
    justify-content: space-between;
  }
  &__content{
    display: flex;
    flex-direction: column;
  }
  &__img {
    @include imgCover(41%);
    border-radius: 1.25em;
  }
  &__items{
    margin-top: auto;
    padding-top: 2.5em;
    @include grid100gap(1.5em);
  }
}

.contact-item{
  @include grid100gap(.5em);
  &__title,
  &__desc{
    line-height: 120%;
    letter-spacing: 0.02em;
  }
  &__title{
    font-size: 1em;
    color: $greyDark;
  }
  &__desc{
    font-size: 1.25em;
    font-weight: 500;
  }
}


@media screen and (max-width: 1400px){
  .about{
    &__grid{
      grid-template-columns: calc(100% - 30.5em - 7.625em) 30.5em;
    }
    &__img{
      padding-bottom: 60.8%;
    }
    &__items{
      margin-top: 2em;
    }
  }

  .about-item{
    padding: .75em 1.5em;
    margin-right: .625em;
    &__num{
      font-size: 1.75em;
    }
  }

  .subscribe{
    &__inner{
      padding: 4em;
      background-image: url(../img/subscribe-bg2.svg);
    }
  }

  .subscribe-form{
    &__btn{
      margin-top: 1.25em;
    }
  }

  .contacts {
    &__grid {
      grid-template-columns: calc(100% - 43.25em - 1.25em) 43.25em;
    }
    &__img {
      padding-bottom: 53.7%;
    }
  }
}


@media screen and (max-width: 992px){
  .about{
    &__grid{
      grid-template-columns: 100%;
    }
    &__img{
      display: none;
    }
    &__desc{
      margin-top: 1em;
    }
  }

  .subscribe{
    &__inner{
      padding: 2.5em 1.5em;
      background-image: url(../img/subscribe-bg3.svg),url(../img/subscribe-bg4.svg);
      background-repeat: no-repeat;
      background-position: top left, right bottom;
      background-size: 6.5em auto, 10.8125em auto;
    }
  }

  .contacts {
    &__grid {
      grid-template-columns: calc(100% - 30em) 29em;
      gap: 1em;
    }
    &__items{
      padding-top: 1em;
      gap: 1em;
    }
  }
}


@media screen and (max-width: 576px){
  .about{
    &__items{
      margin-top: 1em;
      display: block;
    }
  }

  .about-item{
    padding: 1em 1.5em;
    margin: 0 0 .5em;
    display: flex;
    align-items: center;
    justify-content: space-between;
    &__num{
      font-size: 1.25em;
    }
    &__desc{
      font-size: .875em;
      margin: 0 0 0 .5em;
    }
  }

  .subscribe{
    &__inner{
      border-radius: .75em;
    }
    .h2{
      font-size: 1.375em;
    }
    &__desc{
      font-size: .875em;
    }
  }

  .subscribe-form{
    &__btn{
      margin-top: .75em;
      height: 2.6875em;
    }
    .m-check{
      label{
        span{
          font-size: .75em;
        }
      }
    }
  }

  .contacts{
    &__grid{
      grid-template-columns: 100%;
    }
    &__img {
      display: none;
    }
  }

  .contact-item{
    &__title{
      font-size: .875em;
    }
    &__desc{
      font-size: 1.125em;
    }
  }
}
