.modal-bg{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    background-color: $black;
    opacity: .4;
}

.modal {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    overflow-y: auto;
    z-index: 80;
    opacity: 0;
    pointer-events: none;
    //transition: all .3s;
    padding: 1em 0;
    text-align: center;

    &:after {
        content: '';
        display: inline-block;
        height: 100%;
        vertical-align: middle;
        margin-right: -4px;
    }
    &__dialog {
        text-align: left;
        vertical-align: middle;
    }

    &._active {
        opacity: 1;
        pointer-events: unset;
        .modal__bg {
            display: block;
        }
    }
    &__dialog {
        background: $whiteGray;
        width: 32.5em;
        max-width: calc(100% - 2em);
        position: relative;
        z-index: 3;
        padding: 2em;
        border-radius: 1em;
        display: inline-block;
    }
    &__top{
        text-align: center;
        @include grid100gap(.25em);
        margin-bottom: 1.5em;
    }
    .close-btn{
        width: 2em;
        @include center;
        margin: 0 0 1em auto;
        flex-shrink: 0;
        svg{
            width: 100%;
        }
    }
    &__desc{
        color: $greyDark;
    }
    &__icon{
        width: 7.5em;
        display: block;
        margin: 0 auto 1em;
    }
    &__btn{
        width: 100%;
    }
    &__desc2{
        color: $greyDark;
        margin-top: .22em;
    }
}

.modal-form{
    @include grid100gap(.5em);
    &__btn{
        margin-top: 1em;
    }
    .m-check{
        color: $greyDark;
        label:before{
            border-radius: .25em;
        }
    }
}



@media screen and (max-width: 1400px){
    .modal{
        &__dialog {
            width: 24.125em;
            padding: 1.5em;
        }
        &__top{
            margin-bottom: 1.25em;
        }
        .close-btn{
            width: 1.5em;
            margin-bottom: .75em;
        }
    }

    .modal-form{
        &__btn{
            margin-top: .75em;
        }
    }
}



@media screen and (max-width: 992px){
    .modal{
        &__dialog {

        }
    }
}


@media screen and (max-width: 576px){
    .modal{
        padding: 0;
        &__dialog {
            height: $vh;
            border-radius: 0;
            max-width: 100%;
            overflow-y: auto;
            width: 100%;
            padding: 2em 1em;
        }
        &__top{
            margin-bottom: 1.5em;
        }
        &__desc2{
            color: $greyDark;
            margin: .25em auto 0;
            max-width: 11em;
        }
        &__icon{
            width: 11.25em;
        }
    }

    .modal-form{
        &__btn{
            margin-top: 1em;
            height: 2.75em;
        }
    }
}
