.header{
  position: relative;
  z-index: 20;
  &__inner{
    display: flex;
    padding: 2em 0 1em;
    border-bottom: $border;
  }
  &__logo{
    display: block;
    flex-shrink: 0;
    width: 8.4375em;
    margin-right: 3em;
    img{
      width: 100%;
      display: block;
    }
  }
}

.burger{
  display: none;
}

.menu{
  flex-grow: 1;
  &__scroll{
    display: flex;
    align-items: center;
    justify-content: flex-end;
    width: 100%;
  }
  &__btn{
    margin-left: 3em;
    flex-shrink: 0;
    @media screen and (min-width: 993px){
      border: 1px solid $primary;
      &:not(:hover){
        color: $black;
        background-color: transparent;
      }
    }
  }
}

.header-socials,
.nav{
  &__show{
    display: flex;
    align-items: center;
    transition: all .3s;
    cursor: pointer;
    span{
      font-size: 1.125em;
      letter-spacing: .02em;
    }
    svg{
      width: 1.125em;
      margin-left: .25em;
    }
  }
  @media screen and (min-width: 993px){
    &__hidden{
      position: absolute;
      width: 16.625em;
      top: 100%;
      margin-top: -1px;
      padding-top: 1.125em;
      z-index: 10;
      transition: all .3s;
      opacity: 0;
      pointer-events: none;
    }
    &__inner{
      padding: 1.5em .75em 1.5em 1.5em;
      border-radius: .75em;
      border: 1px solid $greyDark;
      @include grid100gap(1em);
      background-color: $white;
    }
  }
}

.nav{
  margin-right: auto;
  display: flex;
  align-items: center;
  &__link{
    font-size: 1.125em;
    letter-spacing: .02em;
    margin-right: 2.22em;
  }
  &__item{
    margin-right: 2.5em;
    position: relative;
    &:hover{
      .nav__show{
        color: $primary;
        path{
          stroke: $primary;
        }
      }
      .nav__hidden{
        opacity: 1;
        pointer-events: unset;
      }
    }
  }
  &__sub-link{
    font-size: 1em;
    line-height: 110%;
    letter-spacing: .01em;
    border-bottom: $border;
    padding-bottom: .25em;
  }
}

.header-socials{
  position: relative;
  &:hover{
    .header-socials__show{
      color: $primary;
      path{
        stroke: $primary;
      }
    }
    .header-socials__hidden{
      opacity: 1;
      pointer-events: unset;
    }
  }
  @media screen and (min-width: 993px){
    .socials{
      @include grid100gap(1em);
      &__item{
        margin: 0 !important;
        width: 100%;
        padding-bottom: .25em;
        border-bottom: $border;
        justify-content: unset;
        &:before{
          content: attr(data-title);
          font-size: 1em;
          line-height: 110%;
          letter-spacing: .01em;
        }
        img{
          display: none;
        }
      }
    }
  }
}

.header-contacts{
  display: flex;
  align-items: center;
  margin-left: 2em;
  &__link{
    @include lastM(0 2em 0 0);
    img{
      display: none;
    }
    span{
      font-size: 1.125em;
      letter-spacing: .02em;
    }
  }
}


@media screen and (max-width: 1400px){
  .header{
    &__logo{
      width: 7.5em;
      margin-right: 2em;
    }
  }

  .nav{
    @media screen and (min-width: 993px){
      font-size: .89em;
    }
  }

  .header-contacts{
    margin-left: 3em;
    @media screen and (min-width: 993px){
      &__link{
        @include lastM(0 1em 0 0);
        img{
          display: block;
          width: 2em;
        }
        span{
          display: none;
        }
      }
    }

  }
}


@media screen and (max-width: 992px){
  .header{
    &__inner{
      padding: 1.5em 0;
      border: none;
      align-items: center;
      justify-content: space-between;
    }
    &__logo{
      position: relative;
      z-index: 11;
      width: 6em;
    }
    &._open{
      .menu{
        opacity: 1;
        pointer-events: unset;
      }
      .burger{
        ._view1{
          opacity: 0;
        }
        ._view2{
          opacity: 1;
        }
      }
    }
  }

  .menu{
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    background-color: $white;
    padding: 4.75em 0 0;
    z-index: 10;
    &__scroll{
      padding: 1em;
      display: flex;
      flex-direction: column;
      justify-content: unset;
      overflow-y: auto;
      height: 100%;
    }
    &__btn{
      margin: 2em 0 0;
      width: 100%;
      height: 3em;
    }
  }

  .burger{
    z-index: 11;
    position: relative;
    width: 1.5em;
    height: 1.5em;
    display: flex;
    img{
      @include centerAbsolute;
      width: 100%;
      transition: inherit;
    }
    ._view2{
      opacity: 0;
      width: 90%;
    }
  }

  .nav{
    width: 100%;
    @include grid100gap(1.25em);
    &__item,
    &__link{
      width: 100%;
    }
    &__sub-link,
    &__link{
      order: -1;
      font-size: 1em;
      line-height: 120%;
      padding-bottom: .5em;
      border-bottom: $border;
    }
    &__show{
      display: none;
    }
    &__inner{
      @include grid100gap(1.25em);
    }
  }

  .header-socials{
    margin-top: 2em;
    &__show{
      display: none;
    }
    .socials{
      justify-content: center;
    }
  }

  .header-contacts{
    order: -1;
    margin: 0 0 2em;
    justify-content: space-between;
    width: 100%;
    &__link{
      color: $greyDark;
      span{
        font-size: .875em;
        line-height: 140%;
      }
    }
  }
}


